.menu-page {
    text-align: center;
  }
  
  .hero-section {
    position: relative;
    height: 500px; /* Adjust the height to make it shorter */
    overflow: hidden; /* Ensure overflow content is hidden */
  }
  
  .hero-image img {
    width: 100%;
    height: auto;
  }
  
  .hero-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.8);
    padding: 20px;
    border-radius: 10px;
    width: 30%;
    text-align: center;
  }
  
  .filters {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  .filters .filter-dropdown, .filters .sort-dropdown {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .filters label {
    font-weight: bold;
  }
  
  .filters select {
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  .filters p {
    margin: 0;
  }
  
  .menu-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  .menu-item {
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    text-align: left;
  }
  
  .menu-item-image {
    background-color: #ccc;
    background-size: cover;
    background-position: center;
    height: 150px;
    overflow: hidden;
    margin-bottom: 10px;
  }
  
  .menu-item h3 {
    margin: 10px 0;
  }
  
  .menu-item p {
    margin: 5px 0;
  }
  
  .menu-item button {
    background-color: #f08080;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
  }
  
  #page-numbers {
    display: flex;
    list-style: none;
    padding: 0;
    justify-content: center;
    margin: 20px 0;
  }
  
  #page-numbers li {
    margin: 0 5px;
    padding: 10px 15px;
    cursor: pointer;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #fff;
  }
  
  #page-numbers li.active {
    background-color: #f08080;
    color: white;
    border-color: #f08080;
  }
  .notification {
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: #f08080;
    color: white;
    padding: 10px;
    border-radius: 5px;
    z-index: 1000;
  }
  